// header start

.header {

  .container {
    position: relative;

    @include r(992) {
      max-width: 100%; }

    &::after {
      content: '';
      display: block;
      background: #1c222a;
      position: absolute;
      right: -100%;
      top: 0;
      bottom: 40px;
      width: 100%;
      margin-right: 15px;

      @include r(992) {
        display: none; } } }

  &__navbar {
    display: flex;
    flex-wrap: nowrap;
    height: 80px;
    margin-bottom: 40px;
    padding: 0;
    position: relative;

    @include r(992) {
      height: 60px;

      &::after {
        content: '';
        position: absolute;
        display: block;
        left: 0;
        right: 0;
        top: 100%;
        height: 40px;
        background: #1C222A;
        z-index: 1; } } }

  &__wrap {
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    background: #1C222A;

    @include r(992) {
      flex-direction: column;
      justify-content: center;
      order: 3;
      flex: 3;
      position: initial;
      max-width: 90px;
      background: none;
      padding-left: 0; } }

  &__navbar-nav {
    background: #1c222a;

    @include r(992) {
      padding-top: 5px;
      height: 100%;
      border-top: 1px solid #ffffff; } }

  .nav-item {
    padding-left: 30px; }

  .logo {
    display: flex;
    align-items: center;
    width: 230px;
    padding: 0 40px 0 0;
    margin: 0;
    overflow: hidden;
    img {
      width: 100%;
      max-width: 240px;

      @include r(1025) {
        max-width: 166px; } }

    @include r(992) {
      order: 2;
      flex: 11;
      padding: 0;
      justify-content: center; } }

  .navbar-toggler {
    border: none;
    z-index: 4;

    @include r(992) {
      order: 1;
      flex: 1;
      max-width: 40px;
      height: 30px;
      padding: 5px;
      margin-left: 20px;

      .icon {
        stroke: #FFB100; } } }

  .navbar-line {
    display: inline-block;
    width: 100%;
    background: #FFB100;
    height: 2px;
    margin: 3px 0;

    &::before,
    &::after {
      content: '';
      display: inline-block;
      width: 100%;
      background: #FFB100;
      height: 2px; } }


  &__navbar-collapse {
    height: 100%;

    @include r(992) {
      order: 3;
      flex: 3;
      position: absolute;
      left: 0;
      right: 6%;
      height: auto;
      z-index: 3;
      top: 0;
      background: #1c222a;
      padding-top: 80px; } }

  &__nav-link {
    font-size: 14px;
    line-height: 19px;
    letter-spacing: 0.02em;
    color: #FFFFFF !important;
    text-transform: uppercase;

    @include r(992) {
      line-height: 16px; } }

  &__action {
    display: flex;
    padding: 10px 0;
    line-height: 0;

    .icon {
      width: 25px;
      height: 25px;
      fill: #FFB100;

      @include r(992) {
        width: 18px;
        height: 18px; } }

    &--city,
    &--lang {
      padding-bottom: 0;

      @include r(992) {
        display: none; } }

    &--account {

      @include r(992) {
        position: absolute;
        top: 100%;
        z-index: 2; } }

    button {
      margin: 0 10px;
      color: #ffffff;
      text-transform: none;
      font-size: 14px;
      line-height: 16px;
      height: 100%;

      @include r(992) {
        margin: 0 7px; } }

    .phone {
      display: inline-block;
      padding: 0;
      margin: 0 7px;
      width: 18px;
      border: none; } }

  .nav-item {

    &--more {
      align-self: center;

      @include r(992) {
        align-self: initial; }

      span {
        color: #fff;

        @include r(992) {
          display: none; } }

      &:hover {
        .more-items {
          display: flex; } } } }

  .more-items {
    display: none;
    position: absolute;
    top: 100%;
    z-index: 5;
    left: 0;
    right: 0;
    background: #1c222a;
    height: 42px;
    padding-left: 15px;

    .nav-item {
      background: #1c222a; }

    &:after {
      content: '';
      background: #1c222a;
      display: block;
      width: 100%;
      margin-right: -100%; }

    @include r(992) {
      display: flex;
      flex-direction: column;
      position: initial;
      padding-left: 0;
      height: auto; } } }

.nav-item-more {
  display: flex;
  align-items: center;

  &:hover {

    .more-items {
      display: flex; } }

  @include r(992) {
    padding-bottom: 5px; }

  span {
    color: #ffffff;

    @include r(992) {
      display: none; } } }

.nav-item-pre-header {

  @include r(992) {
    border-top: 1px solid #ffffff; } }

.pre-header {
  display: flex;
  padding: 10px 0;
  margin-left: 45px;
  left: 0;
  position: absolute;
  top: 100%;

  @include r(992) {
    position: initial;
    display: flex;
    flex-direction: column;
    margin-left: 0; }

  .container {

    &::after {
      display: none; } }

  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include r(767) {
      display: none; } }

  &__item {
    margin: 0 10px;

    @include r(992) {
      margin: 3px 0;
      padding-left: 30px; } }

  &__link {
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: #000;

    @include r(992) {
      font-size: 14px;
      line-height: 16px;
      letter-spacing: 0.02em;
      color: #FFFFFF; } } }

.phone {
  display: flex;
  position: absolute;
  top: 100%;
  right: 15px;
  transform: translateY(50%);
  font-size: 14px;
  line-height: 19px;
  letter-spacing: 0.02em;
  color: #000;

  @include r(992) {
    padding-top: 10px;
    margin-bottom: 20px;
    position: initial;
    padding-left: 30px;
    border-top: 1px solid #ffffff;
    transform: initial;
    line-height: 16px;
    color: #fff; }

  &__icon {
    display: none;
    @include r(992) {
      display: block;
      margin-right: 10px; } } }

.dropdown {
  button {
    margin: 0 10px;
    padding: 0;
    background: none;
    border: none;

    &:hover,
    &:focus,
    &:active {
      outline: none !important;
      background: none !important;
      border: none !important;
      box-shadow: none !important; } } }

// header ends

// footer start

.footer {
  background-color: #000;

  &__wrap {
    padding: 100px 0;

    @include r(1025) {
      padding: 20px 0; } }

  &__item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @include r(1025) {
      align-items: center;

      &:nth-child(2),
      &:nth-child(3),
      &:nth-child(4) {
        display: none; } } }

  .copyright {
    margin-top: 15px;
    color: #fff;
    font-size: 16px;
    line-height: 19px;

    @include r(1025) {
      display: none; }

    a {
      display: inline-block;
      margin-top: 5px;
      color: #fff; } }

  .social {
    position: initial;
    flex-direction: row;
    margin-bottom: 15px;

    @include r(1025) {
      margin-top: 10px; } }

  .link {
    display: inline-block;
    margin: 15px 0;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 0.02em;
    color: #FFFFFF; } }


// footer ends
